@import '../../style.scss';
.stories {
	display: flex;
	gap: 10px;
	height: 250px;
	margin-bottom: 30px;

	@include mobile {
		height: 50px;
		justify-content: space-between;
	}
	@include mobile {
		height: 150px;
		gap: 20px;
	}
	.story {
		flex: 1;
		border-radius: 10px;
		overflow: hidden;
		position: relative;

		@include mobile {
			flex: none;
			width: 50px;
			border-radius: 50%;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		span {
			position: absolute;
			left: 10px;
			bottom: 10px;
			color: aliceblue;
			font-weight: 500;
			@include mobile {
				display: none;
			}
		}
		.button {
			position: absolute;
			bottom: 40px;
			left: 10px;
			color: aliceblue;
			background-color: #5271ff;
			border: none;
			width: 30px;
			border-radius: 50%;
			cursor: pointer;
			font-weight: 500;

			@include mobile {
				left: 0;
				right: 0;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
	}
}
