@import '../../style.scss';
.comments {
	@include themify($themes) {
		img {
			width: 40px;
			height: 40px;
			border-radius: 50%;
			object-fit: cover;
		}
		.write {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 20px;
			margin: 20px 0;
			input {
				flex: 5;
				padding: 10px;
				border: 1px solid themed('border');
				background-color: transparent;
				color: themed('textColor');
			}
			button {
				border: none;
				background-color: #5271ff;
				color: aliceblue;
				padding: 10px;
				cursor: pointer;
				border-radius: 3px;
			}
		}
		.comment {
			margin: 30px 0px;
			display: flex;
			justify-content: space-between;
			gap: 20px;
			.info {
				display: flex;
				flex: 5;
				flex-direction: column;
				gap: 3px;
				align-items: start;
				span {
					font-weight: 500;
				}
				p {
					color: themed('textColorSoft');
				}
			}
			.date {
				display: flex;
				flex: 1;
				align-self: center;
				color: gray;
				font-size: small;
			}
		}
	}
}
