@import '../../style.scss';

.leftbar {
	@include themify($themes) {
		flex: 2;
		position: sticky;
		position: -webkit-sticky;
		top: 70px;
		height: calc(100vh - 70px);
		overflow: scroll;
		background-color: themed(bg);
		color: themed(textColor);

		@include mobile {
			display: none;
		}

		&::-webkit-scrollbar {
			display: none;
		}
		.container {
			padding: 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			hr {
				margin: 20px 0;
				border: none;
				height: 0.5px;
				background-color: lightgray;
			}
			.menu {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: center;
				gap: 20px;
				span {
					font-size: 12px;
				}
				.user {
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 10px;
					img {
						width: 30px;
						height: 30px;
						border-radius: 50%;
						object-fit: cover;
					}
					span {
						font-size: 15px;
					}
				}
				.item {
					display: flex;
					align-items: center;
					justify-content: center;

					gap: 10px;
					img {
						width: 30px;
						height: 30px;
						object-fit: cover;
					}
					span {
						font-size: 15px;
					}
				}
			}
		}
	}
}
